<template>
  <el-dialog
    :model-value="visible"
    title="查看详情"
    width="650px"
    class="custom-drawer"
    @open="getList"
    :before-close="handleClose"
  >
    <el-table :data="pageData.list" max-height="410px">
      <el-table-column
        prop="modificationTime"
        label="修改时间"
        align="center"
      ></el-table-column>
      <el-table-column align="center" label="修改内容对比">
        <template #default="{ row }">
          <el-button type="text" @click="handleLookDetail(row)">查看</el-button>
        </template>
      </el-table-column>
      <template #empty>
        <TableEmpty empty-text="暂无明细"></TableEmpty>
      </template>
    </el-table>
    <!--   分页   -->
    <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
    ></PagePagination>
    <template #footer>
      <el-button type="primary" @click="handleClose">取消</el-button>
    </template>
    <!--查看详情-->
    <DetailDialog v-model:visible="initData.detailVisible" :selectedId="initData.selectedId"></DetailDialog>
  </el-dialog>
</template>
<script lang="ts" setup>
import { shallowReactive, defineAsyncComponent } from "vue";
import { usePage } from "jz-tool-lib";
import { customerPackageRecordApi} from "@api/customer-management-api";
import { useVueRouter } from "@assets/useHook";
const { routeQuery } = useVueRouter();
const DetailDialog = defineAsyncComponent(() => import("./detail-dialog.vue"));
const props = defineProps<{
  visible: boolean;
  packageSettingId: string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();
const initData = shallowReactive({
  detailVisible: false,
  selectedId:""
});

const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: customerPackageRecordApi,
  props: {
    pageNumber: "page",
    pageSize: "pageSize",
  },
  searchParams(){
    return {
      packageSettingId:props.packageSettingId
    }
  }
});
// 关闭弹框
const handleClose = () => {
  emits("update:visible", false);
};
//查看详情
function handleLookDetail(row) {
  initData.detailVisible = true;
  initData.selectedId = row.id
}
</script>
<style lang="scss" scoped>
：deep {
  .el-table {
    width: 100%;
    overflow-y: auto;
  }
}
</style>
